del=\"form.email\"\n                                    type=\"text\"\n                                    class=\"w-full mb-3\"\n                                    placeholder=\"Email\"\n                                    style=\"padding:1rem;\" />\n\n                        <label for=\"password1\" class=\"block text-900 font-medium text-xl mb-2\">Contraseña</label>\n                        <Password id=\"password1\"\n                                    v-model=\"form.password\"\n                                    placeholder=\"Password\"\n                                    :toggleMask=\"true\"\n                                    class=\"w-full mb-3\"\n                                    inputClass=\"w-full\"\n                                    inputStyle=\"padding:1rem\"\n                                    @keyup.enter=\"submit\"></Password>\n\n                        <div class=\"flex align-items-center justify-content-between mb-5\">\n                            <div class=\"flex align-items-center\">\n                                <Checkbox id=\"rememberme1\" v-model=\"checked\" :binary=\"true\" class=\"mr-2\"></Checkbox>\n                                <label for=\"rememberme1\">Recordar me</label>\n                            </div>\n                            <a :href=\"route('register')\" class=\"font-medium no-underline ml-2 text-right cursor-pointer\" style=\"color: var(--primary-color)\">Registrar</a>\n                            <a class=\"font-medium no-underline ml-2 text-right cursor-pointer\" style=\"color: var(--primary-color)\">¿Olvido su contraseña?</a>\n                        </div>\n                        <Button type=\"submit\" @click=\"submit\" label=\"Sign In\" class=\"w-full p-3 text-xl\" :loading=\"form.processing\"></button>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { Head } from '@inertiajs/inertia-vue3';\nimport Checkbox from 'primevue/checkbox';\nimport Button from 'primevue/button';\nimport InputText from 'primevue/inputtext';\nimport Password from 'primevue/password';\nimport Message from 'primevue/message';\n\nexport default {\n    components: {\n        Button,\n        Checkbox,\n        InputText,\n        Password,\n        Message,\n        Head\n    },\n    computed: {\n        logoColor() {\n            return 'dark';\n        }\n    },\n    data() {\n        return {\n            form: this.$inertia.form({\n                email: '',\n                password: '',\n                remember: false\n            })\n        }\n    },\n    methods: {\n        submit() {\n            this.form\n                .transform(data => ({\n                    ... data,\n                    remember: this.form.remember ? 'on' : ''\n                }))\n                .post(this.route('login'), {\n                    onFinish: () => this.form.reset('password'),\n                })\n            console.log(this.form)\n        }\n    }\n}\n</script>\n\n<style scoped>\n.pi-eye {\n    transform:scale(1.6);\n    margin-right: 1rem;\n}\n\n.pi-eye-slash {\n    transform:scale(1.6);\n    margin-right: 1rem;\n}\n</style>\n"],"sourceRoot":""}]);
